import React, { useEffect } from "react";
import * as d3 from "d3";

export default function ProvincePieChart({ id, getData }) {
  useEffect(() => {
    let width = 400;
    let height = Math.min(width, 400);
    const radius = Math.min(width, height) / 15;

    let pie = d3
      .pie()
      .padAngle(0.005)
      .sort(null)
      .value((d) => d.value);

    let arc = d3
      .arc()
      .innerRadius(280 * 0.5)
      .outerRadius(radius - 1);

    let color = d3
      .scaleOrdinal()
      .domain(getData.map((d) => d.description))
      .range([
        "#f2f6f3",
        "rgb(157, 173, 207)",
        "rgb(127, 188, 190)",
        "#b1b5b2",
      ]);

    const arcs = pie(getData);

    const svg = d3
      .select(`#${id}`)
      .append("svg")
      .attr("viewBox", [-width / 2, -height / 2, width, height]);

    svg
      .selectAll("path")
      .data(arcs)
      .join("path")
      .attr("fill", (d) => color(d.data.description))
      .attr("d", arc)
      .append("title")
      .text((d) => `${d.data.description}: ${d.data.value.toLocaleString()}`);

    svg
      .append("g")
      .attr("font-family", "sans-serif")
      .attr("font-size", 28)
      .attr("text-anchor", "middle")
      .selectAll("text")
      .data(arcs)
      .join("text")
      .attr("transform", (d) => `translate(${arc.centroid(d)})`)
      // .call((text) =>
      //   text
      //     .append("tspan")
      //     .attr("y", "-0.4em")
      //     .attr("x", "-5.2em")
      //     .attr("font-size", "1.25rem")
      //     .attr("font-weight", "bold")
      //     .text((d) => d.data.total)
      // )
      .call((text) =>
        text
          .filter((d) => d.endAngle - d.startAngle > 0.25)
          .append("tspan")
          .attr("x", 0)
          .attr("y", "0.7em")
          .attr("fill-opacity", 0.7)
          .text((d) => d.data.value.toLocaleString())
      );
  }, [getData, id]);

  return <div id={id} className="donutchart"></div>;
}
