import React from "react";
import ProvincePieChart from "./ProvincePieChart";

import "./ProvinceGraph.css";

export default function ProvinceGraph({ covidData }) {
  return (
    <div className="Province-Graph">
      {covidData.map((covid, i) => {
        const id = `donutchat${i}`;
        const getData = [
          {
            description: "Tested",
            value: covid.tested,
          },
          {
            description: "Positive",
            value: covid.positive,
          },
          {
            description: "Recovered",
            value: covid.recoveries,
          },
          {
            description: "Deaths",
            value: covid.deaths,
          },
        ];
        return (
          <div key={i}>
            <h3>{covid.province}</h3>
            <ProvincePieChart id={id} getData={getData} />
            <ul>
              {/* <li>
                <span style={{ backgroundColor: "#f2f6f3" }}></span>Tested
              </li> */}
              <li>
                <span style={{ backgroundColor: "#9dadcf" }}></span>Positive
              </li>
              <li>
                <span style={{ backgroundColor: "#7fbcbe" }}></span>Recovered
              </li>
              <li>
                <span style={{ backgroundColor: "#b1b5b2" }}></span>Deaths
              </li>
            </ul>
          </div>
        );
      })}
    </div>
  );
}
