import React, { useState } from "react";

import "./CovidStackedChart.css";
import moment from "moment";

export default function CovidStackedChart({ covidData, totalCovid }) {
  const [selectProvince, setselectProvince] = useState(null);

  const results = selectProvince
    ? covidData.filter((covid) =>
        covid.province
          .toLowerCase()
          .includes(selectProvince.toLocaleLowerCase())
      )
    : covidData;

  const startDate = (value) =>
    moment().subtract(value, "days").format("DD/MM/YYYY");

  const dailyCases = (date, province) =>
    results.map((covid) =>
      covid.daily_cases.filter((daily) => {
        return (daily.date === date || covid.province === province) && daily;
      })
    );

  const totalDailyCases = (day) => dailyCases(day).map((covid) => covid[0]);
  const dayOne = totalDailyCases(startDate(7), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const dayTwo = totalDailyCases(startDate(6), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const dayThree = totalDailyCases(startDate(5), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const dayFour = totalDailyCases(startDate(4), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const dayFive = totalDailyCases(startDate(3), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const daySix = totalDailyCases(startDate(2), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );
  const daySeven = totalDailyCases(startDate(1), selectProvince).reduce(
    (total, day) => {
      const checkDay = day ? day.number : 0;
      return total + checkDay;
    },
    0
  );

  const total =
    dayOne + dayTwo + dayThree + dayFour + dayFive + daySix + daySeven;

  const days = (value) => moment().subtract(value, "days").format("DD MMM");

  return (
    <section className="Stacked-Chart">
      <div className="province-number">
        <header>
          <h4>Daily Positive Cases (Last 7 Days)</h4>
          <select onChange={(event) => setselectProvince(event.target.value)}>
            <option value="">Choose Province</option>
            <option value="Eastern Cape">Eastern Cape</option>
            <option value="Free State">Free State</option>
            <option value="Gauteng">Gauteng</option>
            <option value="KwaZulu-Natal">KwaZulu-Natal</option>
            <option value="Limpopo">Limpopo</option>
            <option value="Mpumalanga">Mpumalanga</option>
            <option value="North West">North West</option>
            <option value="Northern Cape">Northern Cape</option>
            <option value="Western Cape">Western Cape</option>
            {/* <option value="Unallocated">Unallocated</option> */}
          </select>
        </header>
        <div>
          <ul>
            <li>
              <span>{dayOne}</span>
              <span
                className="animated fadeIn"
                style={{
                  height: `${(dayOne / total) * 250}%`,
                  backgroundColor: "#7fbcbe",
                  color: "#000",
                }}
              ></span>
            </li>
            <li>
              <span>{dayTwo}</span>
              <span
                style={{
                  height: `${(dayTwo / total) * 250}%`,
                  backgroundColor: "#7fbcbe",
                  color: "#000",
                }}
              ></span>
            </li>
            <li>
              <span>{dayThree}</span>
              <span
                style={{
                  height: `${(dayThree / total) * 250}%`,
                  backgroundColor: "#7fbcbe",
                  color: "#000",
                }}
              ></span>
            </li>
            <li>
              <span>{dayFour}</span>
              <span
                style={{
                  height: `${(dayFour / total) * 250}%`,
                  backgroundColor: "#7fbcbe",
                  color: "#000",
                }}
              ></span>
            </li>
            <li>
              <span>{dayFive}</span>
              <span
                style={{
                  height: `${(dayFive / total) * 250}%`,
                  backgroundColor: "#7fbcbe",
                  color: "#000",
                }}
              ></span>
            </li>
            <li>
              <span>{daySix}</span>
              <span
                style={{
                  height: `${(daySix / total) * 250}%`,
                  backgroundColor: "#7fbcbe",
                  color: "#000",
                }}
              ></span>
            </li>
            <li>
              <span>{daySeven}</span>
              <span
                style={{
                  height: `${(daySeven / total) * 250}%`,
                  backgroundColor: "#7fbcbe",
                  color: "#000",
                }}
              ></span>
            </li>
          </ul>

          <ul>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
          <ul>
            <li>{days(7)}</li>

            <li>{days(6)}</li>

            <li>{days(5)}</li>

            <li>{days(4)}</li>

            <li>{days(3)}</li>
            <li>{days(2)}</li>
            <li>{days(1)}</li>
          </ul>
        </div>
      </div>
    </section>
  );
}
