import React from "react";
import OverralProvince from "./OverralProvince/OverralProvince";
import { covid } from "../data/covid_data";
// import CoronaGenderBased from "./CoronaGenderBased/CoronaGenderBased";
import ProvinceGraph from "./ProvinceGraph/ProvinceGraph";
import CovidStackedChart from "./CovidStackedChart/CovidStackedChart";

import "./Dashboard.css";

import tested from "./assets/doctor.svg";
import positive from "./assets/ambulance.svg";
import recovered from "./assets/heart-beat.svg";
import deaths from "./assets/healthcare-and-medical.svg";

export default function Dashboard() {
  // const [covidData, setCovidData] = useState(covid);

  const totalCovid = (value) =>
    covid.reduce((total, cases) => {
      return total + cases[value];
    }, 0);

  return (
    <div className="Dashboard">
      <main className="Statistics">
        <div className="overrall">
          <ul>
            <li>
              <span>
                <img src={tested} alt="" />
              </span>
              <span>168643</span>
              <span>Tests Conducted</span>
            </li>
            <li>
              <span>
                <img src={positive} alt="" />
              </span>
              <span>{totalCovid("positive")}</span>
              <span>Positive Cases Identified</span>
            </li>
            <li>
              <span>
                <img src={recovered} alt="" />
              </span>
              <span>{totalCovid("recoveries")}</span>
              <span>Recoveries</span>
            </li>
            <li>
              <span>
                <img src={deaths} alt="" />
              </span>
              <span>{totalCovid("deaths")}</span>
              <span>Deaths</span>
            </li>
          </ul>
        </div>
        <OverralProvince totalCovid={totalCovid} covidData={covid} />
        <div className="provinces">
          {/* <CoronaGenderBased /> */}
          <CovidStackedChart covidData={covid} totalCovid={totalCovid} />
          <ProvinceGraph covidData={covid} />
        </div>
      </main>
    </div>
  );
}
